@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Russo+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-family:'Montserrat', sans-serif}

html,body{margin:0;padding:0}.logo{position:absolute;text-align:center;cursor:default;top:38%;left:50%;transform:translate(-50%, 0%);color:#9147fe}.logo h1{font-family:"Russo One", sans-serif;font-weight:bolder;font-size:3em}.logoCondensed{position:fixed;cursor:default;top:-15px;left:60px;color:#9147fe}.logoCondensed h1{font-family:"Russo One", sans-serif;font-weight:bolder;font-size:2em}.logoCondensed p{display:none}.container{display:flex;flex-direction:row;max-width:100%;max-height:100vh;overflow:hidden}.container .hiddenElement{visibility:hidden;transition:all 0.2s ease-in}.container .show{width:350px;transition:all 0.3s ease-in}.container .hide{width:0px;transition:all 0.3s ease-in}.remove{display:none;transition:all 1s ease-in}.noHeight{transform:scale(0, 0);transition:all 0.2s ease-in}.hoHeight{transform:scale(1, 1);transition:all 0.2s ease-in}

.toggles{display:flex;flex-direction:column;justify-content:space-between;align-items:center;padding:20px 10px}.toggles .toggleIcon,.toggles .darkModeToggleOff .darkIconToggleOff,.toggles .darkModeToggleOn .darkIconToggleOn{cursor:pointer;height:20px;width:20px;background:white;border-radius:50%;position:absolute;top:5px;transition:0.2s all ease-in}.toggles .toggleBG{cursor:pointer;width:50px;height:30px;border-radius:50px;position:relative;transition:0.2s all ease-in}.toggles .darkModeToggleOff{background:gray}.toggles .darkModeToggleOff .darkIconToggleOff{background:white;left:4px}.toggles .darkModeToggleOn{background:#9147fe}.toggles .darkModeToggleOn .darkIconToggleOn{left:26px}.toggles .chatToggle{height:60px;background:#9147fe;left:24px;position:relative;cursor:pointer}.toggles .chatToggle .chatToggleIconOn{height:60%;width:60%;position:relative;top:13px;left:8px;transform:rotate(180deg);transition:transform 0.2s ease-in;-webkit-filter:invert(0.8);filter:invert(0.8)}.toggles .chatToggle .chatToggleIconOn:hover{-webkit-filter:invert(1);filter:invert(1)}.toggles .chatToggle .chatToggleIconOff{height:60%;width:60%;position:relative;top:13px;left:8px;transition:all 0.2s ease-in;-webkit-filter:invert(0.8);filter:invert(0.8)}.toggles .chatToggle .chatToggleIconOff:hover{-webkit-filter:invert(1);filter:invert(1)}.toggles .addStream{height:50px;width:50px;transition:all 0.2s ease-in;outline:none;border-radius:50%;border:none;background:#9147fe}.toggles .addStream:not(:disabled){cursor:pointer}.toggles .addStream:not(:disabled):hover{transform:scale(1.3)}.toggles .addStream:not(:disabled):hover::after{color:white}.toggles .addStream:disabled{cursor:not-allowed;opacity:20%}.toggles .addStream::after{content:"+";font-size:40px;font-weight:bolder;color:#cac8c8;transition:all 0.2s ease-in}.darkMode{transition:all .5s ease-in;background:#2e2e2e}.darkMode .addChannel{background:#2b2929}.darkMode .addChannel .channelInput{border:2px solid transparent}.darkMode .addChannel .modalHeader span{color:white}.lightMode{transition:all .5s ease-in;background:#f5f5f5}.lightMode .chatTabs{background:white}.lightMode .chatTabs li,.lightMode .chatTabs .selectedTab{color:#9147fe}.lightMode .chatTabs li:hover,.lightMode .chatTabs .selectedTab:hover{color:#9147fe}

.screenOverlay{position:absolute;background:rgba(0,0,0,0.532);width:100vw;height:100vh;z-index:3}.screenOverlay .addChannel{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);z-index:10;width:320px;height:175px;display:flex;flex-direction:column;justify-content:space-evenly;padding:10px}.screenOverlay .addChannel .channelInput{box-sizing:border-box;outline:none;width:100%;border-radius:3px}.screenOverlay .addChannel .channelInput:focus{border:2px solid #9147fe}.screenOverlay .addChannel .modalElement{padding:8px;margin:10px 0px 10px}.screenOverlay .addChannel .modalHeader{display:flex;flex-direction:row;justify-content:space-between;align-items:center;padding:0px}.screenOverlay .addChannel .modalHeader span{display:inline-block}.screenOverlay .addChannel .done{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;border-radius:50px;border:none;align-self:center}.screenOverlay .addChannel .done:not(:disabled){cursor:pointer;background:#9147fe;color:white}.screenOverlay .addChannel .error{text-align:center;font-size:13px;color:red;margin:0}.screenOverlay .addChannel .modalClose{text-align:right;cursor:pointer;font-size:20px;font-weight:bold}.addChannel{transition:all .5s ease-in;background:#f5f5f5}.addChannel .channelInput{border:2px solid gray}.addChannel .modalHeader span{color:black}

.chatFrame{width:350px;height:100vh;position:relative;display:flex;flex-direction:column}

.chatTabs{list-style-type:none;margin:0;padding:5px 4px;display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-evenly;overflow:hidden;background:#18181b;height:10vh}.chatTabs li{padding:3px;color:#dddddd;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;font-weight:bold;transition:all 0.15s ease-in}.chatTabs li:hover{cursor:pointer;color:#ffffff;border-bottom:3px solid #9147fe}.chatTabs .selectedTab{color:#ffffff;border-bottom:3px solid #9147fe;line-height:20px}

.iframeContainer iframe{height:90vh;width:350px}

.child{position:relative}.child .helper{position:absolute;height:45px;bottom:-45px;background:#9147fe;width:100%;display:flex;flex-direction:row;justify-content:space-evenly;align-items:center;border-radius:0px 0px 8px 8px;z-index:12}.child .helper .hoverIcon{padding:10px;margin:0px 20px;cursor:pointer;height:30px;-webkit-filter:invert(0.8);filter:invert(0.8);transition:all 0.2s ease-in}.child .helper .hoverIcon:hover{-webkit-filter:invert(1);filter:invert(1);transform:scale(1.3)}.videoFrame{display:grid;grid-column-gap:5px;grid-row-gap:15px;width:100%;padding:60px 0px 60px 60px}.videoFrame .child{width:100%}.videoFrame iframe{height:100%;width:100%}.parent1{grid-template-columns:repeat(1, 1fr)}.parent2{grid-template-columns:repeat(2, 1fr)}.parent3{grid-template-columns:repeat(3, 1fr)}

